  color: #42b983;
}
table.inputTable {
  border: solid 1px black; /*  */
  padding: 10px; /*  */
  border-collapse:  collapse; /*  */
  background-color: #e2e2e1;
}
tr.outputTopic{
  background-color: rgb(255, 191, 202);
}
td.inputTitle {
  border-bottom: solid 3px #9c9c94;
}
td.inputItemHeadline {
  white-space: nowrap; /*  */
  vertical-align: top;
  text-align: left;
}
td.inputItemTitle {
  white-space: nowrap; /*  */
  vertical-align: top;
  text-align: right;
}
td.top {
  vertical-align: top;
}
td.outputStr {
  white-space: nowrap; /*  */
  text-align: left;
}
td.outputContents {
  text-align: left;
  min-width: 150px;
}
td.inputCheckBox {
  text-align: left;
}
td.inputKeyword {
  text-align: right;
}
input.price{
  width: 60px;
  text-align: right;
}
td.outputNum{
  margin-left: auto;
  text-align: right;
}
td.outputNumRed{
  margin-left: auto;
  text-align: right;
  color: red;
}
.asc::after {
  content: ""
}
.desc::after {
  content: ""
}
table.outputTable{
  width: 100%;
}
</style>


